* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}
body {
  font-family: var(--font-geist-sans, system-ui, -apple-system, sans-serif);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: rgb(248 250 252);
}
::-webkit-scrollbar-thumb {
  background: rgb(203 213 225);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(148 163 184);
}
.dark-scrollbar::-webkit-scrollbar-track {
  background: rgb(15 23 42);
}
.dark-scrollbar::-webkit-scrollbar-thumb {
  background: rgb(51 65 85);
}
.dark-scrollbar::-webkit-scrollbar-thumb:hover {
  background: rgb(71 85 105);
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slide-up {
  from {
    transform: translateY(10px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-down {
  from {
    transform: translateY(-10px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes scale-in {
  from {
    transform: scale(0.95);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
.animate-fade-in {
  animation: fade-in 0.3s ease-out;
}
.animate-slide-up {
  animation: slide-up 0.3s ease-out;
}
.animate-slide-down {
  animation: slide-down 0.3s ease-out;
}
.animate-scale-in {
  animation: scale-in 0.2s ease-out;
}
.glass {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}
.glass-dark {
  background: rgba(15, 23, 42, 0.7);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}
.gradient-bg {
  background: linear-gradient(135deg, rgb(248 250 252) 0%, rgb(239 246 255) 50%, rgb(238 242 255) 100%);
}
.gradient-primary {
  background: linear-gradient(135deg, rgb(37 99 235) 0%, rgb(79 70 229) 100%);
}
.gradient-success {
  background: linear-gradient(135deg, rgb(22 163 74) 0%, rgb(5 150 105) 100%);
}
.gradient-warning {
  background: linear-gradient(135deg, rgb(249 115 22) 0%, rgb(239 68 68) 100%);
}
.hover-lift {
  transition: all 0.2s ease;
}
.hover-lift:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 8px 10px -6px rgba(0, 0, 0, 0.1);
}
.hover-glow:hover {
  box-shadow: 0 0 20px rgba(59, 130, 246, 0.4);
}
.focus-ring {
  outline: none;
  transition: all 0.2s ease;
}
.focus-ring:focus-visible {
  ring: 4px;
  ring-color: rgba(59, 130, 246, 0.2);
  border-color: rgb(59 130 246);
}
.card {
  background: white;
  border-radius: 1rem;
  border: 1px solid rgb(226 232 240);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.05);
}
.card-hover {
  transition: all 0.2s ease;
}
.card-hover:hover {
  border-color: rgb(191 219 254);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05), 0 2px 4px -2px rgba(0, 0, 0, 0.05);
}
.message-user {
  background: linear-gradient(135deg, rgb(37 99 235) 0%, rgb(79 70 229) 100%);
  color: white;
  border-radius: 1rem;
  border-top-right-radius: 0.25rem;
}
.message-assistant {
  background: white;
  color: rgb(51 65 85);
  border-radius: 1rem;
  border-top-left-radius: 0.25rem;
  border: 1px solid rgb(226 232 240);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
.btn-primary {
  background: linear-gradient(135deg, rgb(37 99 235) 0%, rgb(79 70 229) 100%);
  color: white;
  border-radius: 0.75rem;
  padding: 0.625rem 1rem;
  font-weight: 500;
  font-size: 0.875rem;
  transition: all 0.2s ease;
  border: none;
  cursor: pointer;
}
.btn-primary:hover:not(:disabled) {
  box-shadow: 0 10px 15px -3px rgba(37, 99, 235, 0.3);
  transform: scale(1.02);
}
.btn-primary:active:not(:disabled) {
  transform: scale(0.98);
}
.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.btn-secondary {
  background: white;
  color: rgb(51 65 85);
  border-radius: 0.75rem;
  padding: 0.625rem 1rem;
  font-weight: 500;
  font-size: 0.875rem;
  transition: all 0.2s ease;
  border: 2px solid rgb(226 232 240);
  cursor: pointer;
}
.btn-secondary:hover {
  border-color: rgb(59 130 246);
  background: rgb(239 246 255);
}
.input-primary {
  width: 100%;
  padding: 1rem 1.25rem;
  border-radius: 1rem;
  border: 2px solid rgb(226 232 240);
  background: white;
  font-size: 0.875rem;
  transition: all 0.2s ease;
  outline: none;
}
.input-primary:focus {
  border-color: rgb(59 130 246);
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.1);
}
.input-primary::placeholder {
  color: rgb(148 163 184);
}
.badge {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.625rem;
  border-radius: 0.5rem;
  font-size: 0.75rem;
  font-weight: 500;
}
.badge-blue {
  background: rgb(239 246 255);
  color: rgb(37 99 235);
}
.badge-green {
  background: rgb(240 253 244);
  color: rgb(22 163 74);
}
.badge-orange {
  background: rgb(255 247 237);
  color: rgb(249 115 22);
}
.badge-slate {
  background: rgb(241 245 249);
  color: rgb(71 85 105);
}
.table-auto {
  width: 100%;
  border-collapse: collapse;
}
.table-auto thead {
  background: rgb(248 250 252);
}
.table-auto th {
  padding: 1rem 1.5rem;
  text-align: left;
  font-size: 0.75rem;
  font-weight: 600;
  color: rgb(71 85 105);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.table-auto td {
  padding: 1rem 1.5rem;
  border-top: 1px solid rgb(241 245 249);
}
.table-auto tbody tr {
  transition: background-color 0.15s ease;
}
.table-auto tbody tr:hover {
  background: rgb(248 250 252);
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.animate-spin {
  animation: spin 1s linear infinite;
}
@keyframes skeleton-loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
.skeleton {
  background: linear-gradient(90deg, rgb(241 245 249) 25%, rgb(226 232 240) 50%, rgb(241 245 249) 75%);
  background-size: 200% 100%;
  animation: skeleton-loading 1.5s ease-in-out infinite;
  border-radius: 0.5rem;
}
.tooltip {
  position: relative;
}
.tooltip::before {
  content: attr(data-tooltip);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(-8px);
